'use client'

import { useState } from 'react'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"

interface ReportData {
  totalInspections: number
  completed: number
  pending: number
  invoiced: number
  totalRevenue: number
}

const mockData: ReportData = {
  totalInspections: 25,
  completed: 18,
  pending: 7,
  invoiced: 15,
  totalRevenue: 75000
}

export default function RapportDashboard() {
  const [selectedPeriod, setSelectedPeriod] = useState('all')
  const [reportType, setReportType] = useState('vedlikehold')

  const handleGenerateReport = () => {
    // Her kan du implementere faktisk rapportgenerering
    alert(`Genererer ${reportType}-rapport for periode: ${selectedPeriod}`)
  }

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Rapporter</h1>
        <Button 
          onClick={handleGenerateReport}
          className="bg-blue-600 hover:bg-blue-700"
        >
          Generer rapport
        </Button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
        <Card className="p-4">
          <h3 className="font-medium mb-2">Rapporttype</h3>
          <Select value={reportType} onValueChange={setReportType}>
            <SelectTrigger>
              <SelectValue placeholder="Velg rapporttype" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="vedlikehold">Vedlikeholdsrapport</SelectItem>
              <SelectItem value="fakturering">Faktureringsrapport</SelectItem>
              <SelectItem value="kunder">Kunderapport</SelectItem>
            </SelectContent>
          </Select>
        </Card>

        <Card className="p-4">
          <h3 className="font-medium mb-2">Periode</h3>
          <Select value={selectedPeriod} onValueChange={setSelectedPeriod}>
            <SelectTrigger>
              <SelectValue placeholder="Velg periode" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">Hele tiden</SelectItem>
              <SelectItem value="2024">2024</SelectItem>
              <SelectItem value="2023">2023</SelectItem>
              <SelectItem value="last6months">Siste 6 måneder</SelectItem>
              <SelectItem value="last30days">Siste 30 dager</SelectItem>
            </SelectContent>
          </Select>
        </Card>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <Card className="p-6">
          <h3 className="text-lg font-semibold mb-2">Vedlikehold</h3>
          <div className="space-y-2">
            <div className="flex justify-between">
              <span className="text-gray-600">Totalt antall</span>
              <span className="font-medium">{mockData.totalInspections}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600">Fullført</span>
              <span className="font-medium text-green-600">{mockData.completed}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600">Ventende</span>
              <span className="font-medium text-yellow-600">{mockData.pending}</span>
            </div>
          </div>
        </Card>

        <Card className="p-6">
          <h3 className="text-lg font-semibold mb-2">Fakturering</h3>
          <div className="space-y-2">
            <div className="flex justify-between">
              <span className="text-gray-600">Fakturert (antall)</span>
              <span className="font-medium">{mockData.invoiced}</span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600">Total omsetning</span>
              <span className="font-medium text-green-600">
                {mockData.totalRevenue.toLocaleString('nb-NO')} kr
              </span>
            </div>
            <div className="flex justify-between">
              <span className="text-gray-600">Gjennomsnitt per oppdrag</span>
              <span className="font-medium">
                {(mockData.totalRevenue / mockData.invoiced).toLocaleString('nb-NO')} kr
              </span>
            </div>
          </div>
        </Card>

        <Card className="p-6">
          <h3 className="text-lg font-semibold mb-2">Status oversikt</h3>
          <div className="space-y-4">
            <div className="relative pt-1">
              <div className="text-xs mb-1">Fullført ({(mockData.completed / mockData.totalInspections * 100).toFixed(0)}%)</div>
              <div className="overflow-hidden h-2 text-xs flex rounded bg-gray-200">
                <div 
                  className="bg-green-500" 
                  style={{ width: `${(mockData.completed / mockData.totalInspections * 100)}%` }}
                />
              </div>
            </div>
            <div className="relative pt-1">
              <div className="text-xs mb-1">Fakturert ({(mockData.invoiced / mockData.totalInspections * 100).toFixed(0)}%)</div>
              <div className="overflow-hidden h-2 text-xs flex rounded bg-gray-200">
                <div 
                  className="bg-blue-500" 
                  style={{ width: `${(mockData.invoiced / mockData.totalInspections * 100)}%` }}
                />
              </div>
            </div>
          </div>
        </Card>
      </div>
    </div>
  )
} 